@import "../variable";

$base-size: $heading-base-size;
$base: $html;
// 제곱함수
@function pow($num, $n) {
	$val : $num;
	@for $i from 1 to $n {
		$num : $num*$val; }
	@return $num; }

@mixin inith($n) {
	$fs : pow($heading-ratio, $n)*$base-size;
	$line-num: ceil($fs/$base-line);
	$line: ($line-num*$base-line)/$base;
	font-size: $fs/$base*1rem;
	line-height: $line*1rem; }

$cr: 7;
@for $i from 1 to 7 {
	$cr : $cr - 1;
	h#{$cr} {
		@include inith($i); } }
