// ==============================
// user-set
// ==============================
$body-size: 20;					// 기본 font-size가 바뀌기 때문에 지정해줌

$heading-base-size: 16;			// heading의 크기 늘어날 base-size  (rem, 값을 기준으로 하기 때문에 적용)
$heading-ratio: 1.25;			// 늘어날 비율
$base-line: 10;					// base-line에 따라 line-height 지정

$now-scale: 2;					// 시안의 현재 적용된 크기( 배율 )
$can-min-scale: 4;				// 최대로 줄일수 있는 크기
$see-scale: 1;					// 처음에 보여줄 크기

// ******** -설명-
///	들어가기에 앞서 모든 값은 (크기에 따라 바뀌는 값 제외한), rem으로 쓸것! rem(value)
/// *	1.	- 현재 시안이 2배율, 전체 size를 최대 1/2까지 줄이고 싶다 [(can-min-sclae : 2) => $html : 10*$now-ratio 적용 (10px 이하로 크기 안줄어듦 )],
///		- 위 내용에 근거한 예제 ) Ex) 1/4까지 줄일수 있게 하고싶다 => can-min-sclae: 4, html의 font-size를 10으로 변경해주면 1/4만큼 줄어듦
///	2.	크기를 늘리는건 구애받지 않으므로 제외, 현재 적용된 html의 font-size를 기준으로 늘리고싶은 크기 정해서 적용 [공식 2]
/// *	공식1 : size*(now-scale/move- scale)
/// * 공식2 : root-font*1.5  (1.5배만큼 커짐)
// ********

// ==============================
// END
// ==============================

$html : 10*$can-min-scale;
$body: $body-size/$html;
